            // console.log(this.locations[response[i].building_id - 1]);
            if (response[i].building_id != 1) {
              this.locations[response[i].building_id - 1].data =
                response[i].ec + " KWH, Updated on " + response[i].date;
            }
            // console.log(this.locations);
          }
        })
        .catch((error) => {
          console.log(error);
        });
